import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';

import { clearActiveBinProduct, clearActiveBinUser } from '../../actions/Bin';

import { BinProductsDashboardTable } from './tables/Bin/BinProductsDashboardTable';
import { NewBinProductsDashboardTable } from './tables/NewBinProductsDashboardTable';


export const BinProductsDashboard = () => { 

    const dispatch = useDispatch();

    useEffect(() => {
        
        dispatch( clearActiveBinProduct() );
        dispatch( clearActiveBinUser() );
    
      
    }, []);

    return (
            <>
 
                {/* <BinProductsDashboardTable /> */}

                <NewBinProductsDashboardTable />

            </>
    );
};