import React, { useState, useRef, createRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Events, scrollSpy, animateScroll as scroll } from 'react-scroll'

import { Image } from 'cloudinary-react';

import { Button } from '@mui/material';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import IconButton from '@mui/material/IconButton';
import Container from '@mui/material/Container';
import EditIcon from '@mui/icons-material/Edit';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DoneOutlineIcon from '@mui/icons-material/DoneOutline';
import Typography from '@mui/material/Typography';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { prepareProducts } from '../../../helpers/prepareProducts';
import { userStartDeletedCart, userStartUpdatedCart } from '../../../actions/users';
import { useNavigate } from 'react-router';
import { activeProduct } from '../../../actions/products';
import { uiOpenDialogDelete, uiOpenProductModalEdit } from '../../../actions/ui';


export const CardsMainHome = () => {

    const refs = useRef([createRef(),
    createRef(),
    createRef(),
    createRef(),
    createRef(),
    createRef(),
    createRef(),
    createRef()]);

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const { uid, role, cart: cartProducts } = useSelector(state => state.auth);
    const { filteredProducts } = useSelector(state => state.product);

    const [anchorEl, setAnchorEl] = useState(null);
    const [cardRefs, setCardRefs] = useState([]);
    const [yWindow, SetyWindow] = useState(0);


    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));





    window.onscroll = () => {

        if (sm) {

            SetyWindow(window.scrollY);
        }

    }


    const conditionHover = (i) => {

        if (sm) {
            if (
                (
                    refs.current[i].current?.getBoundingClientRect().y > -75
                    &&
                    (refs.current[i].current?.getBoundingClientRect().y + 200) < 500
                )
                || (i === 0 && yWindow === 0)
            ) {
                return true;
            } else {
                return false;
            }
        } else {
            return false;
        }

    }

    const handleAddCart = ({ cart }) => {

        if (!uid) {

            navigate('login', { replace: true })
        } else {

            dispatch(userStartUpdatedCart(cart, uid));
        }

    }

    const handleDeleteCart = ({ cart }) => {

        if (!uid) {

            navigate('login', { replace: true })
        } else {

            dispatch(userStartDeletedCart(cart, uid));
        }
    }


    const handleEditButton = (e) => {
        e.preventDefault();

        dispatch(uiOpenProductModalEdit());

        handleLogout();

    }

    const handleDeleteButton = (e) => {
        e.preventDefault();

        dispatch(uiOpenDialogDelete())

        handleLogout();

    }

    // Normal menu
    const handleMenu = (event, product) => {

        const productSelected = (filteredProducts.filter(
            e => (e._id === product.id)
        ));

        dispatch(activeProduct(productSelected[0]));

        setAnchorEl(event.currentTarget);
    };

    const handleLogout = () => {
        handleClose();
    }

    const handleClose = () => {
        setAnchorEl(null);
    };



    if (filteredProducts) {

        var cleanProducts = prepareProducts(filteredProducts);
    }

    return (
        <>
            {
                (filteredProducts && filteredProducts.length !== 0)
                    ?
                    cleanProducts[0].map((e, i) => (
                        <Card
                            key={e.id}
                            ref={refs?.current[i]}
                            // ref={ref}
                            sx={{
                                width: (sm) ? '90%' : '30ch',
                                height: (conditionHover(i)) ? '45ch' : '40ch',
                                transition: 'all 0.5s ease-out;',
                                marginTop: (sm) ? '5%' : '2.5%',
                                objectFit: 'contain',
                                overflow: 'cover',
                                '& #imageContainer': {
                                    display: 'flex',
                                    justifyContent: 'center',
                                    margin: 'auto',
                                    width: (sm) ? '100%' : '30ch',
                                    height: '30ch',
                                    cursor: 'pointer',
                                    img: {
                                        display: 'flex',
                                        width: 'auto',
                                        justifySelf: 'center',
                                        margin: 'auto',
                                    },
                                },
                                '& #productImage': {
                                    maxWidth: (sm) ? '80%' : '30ch',
                                    maxHeight: '30ch',
                                    marginLeft: 0,
                                    marginRight: '5vh',
                                },
                                '& #title': {
                                    marginTop: '2.5%',
                                    visibility: (conditionHover(i)) ? 'visible' : 'hidden',
                                },
                                '& #price': {
                                    marginTop: '7.5%',
                                },
                                '& #iconsContainer': {
                                    position: 'absolute',
                                    display: 'flex',
                                    justifyContent: 'flex-end',
                                    alignItems: 'center',
                                    width: (sm) ? '80%' : '30ch',
                                    marginRight: (sm) && '2.5%',
                                    height: '5ch',
                                    // visibility: 'hidden',
                                    zIndex: 110,
                                },
                                '& #iconsButtons': {
                                    display: 'flex',
                                    justifyContent: 'flex-end',
                                    alignItems: 'center',
                                    position: 'relative',
                                    width: '5ch',
                                    height: '5ch',
                                },
                                '& #goToCartButton': {
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    backgroundColor: '#F29102',
                                    marginRight: (sm) ? '10%' : '2.5%',
                                    color: '#fff',
                                    borderRadius: '10px',
                                    height: (sm) ? '65%' : '55%',
                                    width: (sm) ? '35%' : '50%',
                                    visibility: (conditionHover(i))
                                    ?
                                    (cartProducts) ? ((cartProducts.filter(event => event._id === e.id).length !== 0) ? 'visible' : 'hidden') : 'hidden'
                                    : 'hidden',
                                    zIndex: 100
                                },
                                '& #productMenuIcon': {
                                    visibility: (conditionHover(i)) ? 'visible' : 'hidden',
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    width: '5ch',
                                    height: '5ch',
                                    zIndex: 100
                                },
                                '& #ShoppingCartIcon': {
                                    position: 'absolute',
                                    right: (sm) ? (role === 'USER_ROLE' || role === undefined) ? '2.5%' : '15%' : (role === 'USER_ROLE' || role === undefined) ? '15%' : '0%',
                                    visibility: (conditionHover(i))
                                        ?
                                        (cartProducts) ? ((cartProducts.filter(event => event._id === e.id).length === 0) ? 'visible' : 'hidden') : 'visible'
                                        :
                                        'hidden',
                                    zIndex: 100,
                                    '& .MuiSvgIcon-root': {
                                        color: '#F29102',
                                    } 
                                },
                                '& #SuccessCartIcon': {
                                    position: 'absolute',
                                    right: (sm) ? (role === 'USER_ROLE' || role === undefined) ? '2.5%' : '15%' : (role === 'USER_ROLE' || role === undefined) ? '15%' : '0%',
                                    color: '#0B5345',
                                    visibility: (conditionHover(i))
                                        ?
                                        (cartProducts) ? ((cartProducts.filter(event => event._id === e.id).length !== 0) ? 'visible' : 'hidden') : 'hidden'
                                        : 'hidden',
                                    zIndex: 100
                                },
                                ':hover':
                                    (!sm)
                                    && {
                                        '& #title': {
                                            visibility: 'visible',
                                        },
                                        '& #EditIcon': {
                                            visibility: 'visible',
                                        },
                                        '& #productMenuIcon': {
                                            visibility: 'visible',
                                        },
                                        '& #ShoppingCartIcon': {
                                            visibility: (cartProducts) ? ((cartProducts.filter(event => event._id === e.id).length === 0) ? 'visible' : 'hidden') : 'visible',
                                        },
                                        '& #SuccessCartIcon': {
                                            visibility: (cartProducts) && ((cartProducts.filter(event => event._id === e.id).length !== 0) ? 'visible' : 'hidden'),
                                        },
                                        '& #goToCartButton': {
                                            visibility: (cartProducts) && ((cartProducts.filter(event => event._id === e.id).length !== 0) ? 'visible' : 'hidden'),
                                        },
                                        height: '45ch',
                                        transition: 'all 0.5s ease-out;',
                                    },
                            }}>
                            <div id='iconsContainer'>

                                <Button
                                    id='goToCartButton'
                                    onClick={() => navigate('/cart', { replace: true })}
                                >
                                    <Typography fontSize={12} variant="body2">
                                        Ir al carrito
                                    </Typography>
                                </Button>

                                <div id='iconsButtons'>
                                    <IconButton
                                        id='ShoppingCartIcon'
                                        size="large"
                                        aria-label="account of current user"
                                        aria-controls="menu-appbar"
                                        aria-haspopup="true"
                                        color="inherit"
                                        onClick={() => handleAddCart({ cart: e })}
                                    >
                                        <ShoppingCartIcon />
                                    </IconButton>

                                    <IconButton
                                        id='SuccessCartIcon'
                                        size="large"
                                        aria-label="account of current user"
                                        aria-controls="menu-appbar"
                                        aria-haspopup="true"
                                        color="inherit"
                                        onClick={() => handleDeleteCart({ cart: e })}
                                    >
                                        <DoneOutlineIcon />
                                    </IconButton>
                                </div>

                                {
                                    (role !== 'USER_ROLE' && role)
                                    &&

                                    <div id='productMenuIcon'>
                                        <IconButton
                                            size="large"
                                            aria-label="account of current user"
                                            aria-controls="menu-appbar"
                                            aria-haspopup="true"
                                            onClick={(event) => handleMenu(event, e)}
                                            color="inherit"
                                            
                                        >
                                            <MoreVertIcon />
                                        </IconButton>
                                        <Menu
                                            id="menu-appbar"
                                            anchorEl={anchorEl}
                                            anchorOrigin={{
                                                vertical: 'bottom',
                                                horizontal: 'right',
                                            }}
                                            sx={{
                                            }}
                                            transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            open={Boolean(anchorEl)}
                                            onClose={handleClose}
                                        >
                                            <MenuItem onClick={handleEditButton}>Editar</MenuItem>
                                            <MenuItem onClick={handleDeleteButton}>Eliminar</MenuItem>
                                        </Menu>
                                    </div>
                                }

                            </div>

                            <div
                                id='imageContainer'
                            >

                                {
                                    (e.img[0].imageUrl !== undefined)
                                        ?
                                        <Image
                                            id='productImage'
                                            cloudName="the-kings-company"
                                            publicId={e.img[0].imageUrl}
                                            alt='Product'
                                            onClick={() => navigate(`/product/${e.id}`, { replace: true })}
                                        />
                                        :
                                        <Image
                                            id='productImage'
                                            cloudName="the-kings-company"
                                            publicId={e.img[0]}
                                            onClick={() => navigate(`/product/${e.id}`, { replace: true })}
                                            alt='Product' />
                                }
                            </div>

                            <CardContent>
                                <Typography id='price' sx={{ fontSize: '18px' }} gutterBottom variant="h5" component="div">
                                    {`$${new Intl.NumberFormat('es-IN').format(e.price)}`}
                                </Typography>

                                <Typography id='title' variant="body2" color="text.secondary">
                                    {e.name}
                                </Typography>
                            </CardContent>
                        </Card>
                    ))
                    :
                    <Container sx={{
                        position: 'relative',
                        backgroundColor: '#fff',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        height: '20vh',
                        width: '60vw',
                        borderRadius: '5px',
                        boxShadow: '5px 5px gray',
                        margin: 'auto',
                        marginTop: '7.5%',
                    }}>

                        <Typography fontSize={20} id='title' variant="body2" color="text.primary">
                            No se ha encontrado ningn producto
                        </Typography>

                    </Container>
            }

        </>

    );
}
