import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import IconButton from '@mui/material/IconButton';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';

import { searchProducts } from '../../actions/products';

export const CategoriesBar = ({ setSearchText }) => {

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const { categories } = useSelector(state => state.categories);

    const [anchorCategoriesMenu, setAnchorCategoriesMenu] = useState(null);

    const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));

    // Categories menu
    const handleCategoriesMenu = (event) => {
        setAnchorCategoriesMenu(event.currentTarget);
    };

    const handleCloseCategoriesMenu = () => {
        setAnchorCategoriesMenu(null);
    };

    const handleCategory = (e, { categorySelected }) => {
        e.preventDefault();

        setSearchText('')

        navigate(`/?c=${categorySelected.name}&page=1`, { replace: true });

        dispatch(searchProducts(categorySelected.name));

    }

    return (
        <>
            {
                (categories !== undefined)
                &&
                <AppBar
                    sx={{
                        position: 'fixed',
                        top: '10vh',
                        width: '100%',
                        height: '50px',
                        display: 'flex',
                        justifyContent: 'center',
                        backgroundColor: '#21618C ',
                        '& #categoriesContainer': {
                            position: 'relative',
                            display: 'flex',
                            padding: '1% 2.5%',
                            margin: 'auto',
                            marginRight: '5%',
                            cursor: 'pointer',
                            ':hover': {
                                backgroundColor: '#1B4F72',
                                transition: 'all 0.5s ease'
                            }
                        }
                    }}>
                    <Toolbar sx={{
                        position: 'relative',
                        width: '100%',
                        display: 'flex',
                        flexWrap: 'wrap',
                        justifyContent: 'center',
                    }}>

                        {
                            (categories.length >= 3)
                            &&
                            <div
                            >
                                <Menu
                                    anchorEl={anchorCategoriesMenu}
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'left',
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'left',
                                    }}
                                    open={Boolean(anchorCategoriesMenu)}
                                    onClose={handleCloseCategoriesMenu}
                                >
                                    {
                                        (categories.length > 3)
                                        &&
                                        categories.map(
                                            (event, index) =>
                                                (index > ((!md) ? 3 : 2))
                                                &&
                                                <MenuItem
                                                    key={event._id}
                                                    onClick={(e) => {
                                                        handleCloseCategoriesMenu();
                                                        handleCategory(e, { categorySelected: event });
                                                    }}
                                                >
                                                    {event.name.charAt(0).toUpperCase() + event.name.slice(1)}
                                                </MenuItem>
                                        )
                                    }
                                </Menu>

                                <IconButton
                                    size="large"
                                    edge="start"
                                    color="inherit"
                                    aria-label="menu"
                                    onClick={handleCategoriesMenu}
                                >
                                    <FormatListBulletedIcon />
                                </IconButton>
                            </div>
                        }

                        {
                            categories.map(
                                (event, index) =>
                                    (index <= ((!md) ? 3 : 2))
                                    &&
                                    <div
                                        key={event._id}
                                        id='categoriesContainer'
                                        onClick={(e) => handleCategory(e, { categorySelected: event })}
                                    >
                                        <Typography sx={{
                                            display: 'flex',
                                            justifySelf: 'center',
                                            color: '#fff',
                                            boderRight: '1px solid #000',
                                            margin: 'auto',
                                        }}>
                                            {event.name.charAt(0).toUpperCase() + event.name.slice(1)}
                                        </Typography>
                                    </div>

                            )
                        }



                    </Toolbar>
                </AppBar>
            }
        </>
    );
};