import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';

import { clearActiveBinProduct, clearActiveBinUser } from '../../actions/Bin';

import { BinUsersDashboardTable } from './tables/Bin/BinUsersDashboardTable';
import { NewBinUsersDashboardTable } from './tables/NewBinUsersDashboardTable copy';


export const BinUsersDashboard = () => { 

    const dispatch = useDispatch();


    useEffect(() => {

        dispatch(clearActiveBinProduct());
        dispatch(clearActiveBinUser());


    }, []);

    return (
            <>

                {/* <BinUsersDashboardTable /> */}

                <NewBinUsersDashboardTable />

            </>
    );
};