import React, { forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { uiCloseErrorAlert } from '../../../actions/ui';

const Alert = forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});


export const ErrorAlert = () => {

    const dispatch = useDispatch();

    const { errorAlert } = useSelector( state => state.ui );

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));

    

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        dispatch( uiCloseErrorAlert() )
    };


    return (
            <Snackbar open={errorAlert.status} autoHideDuration={6000} onClose={handleClose} >
                <Alert onClose={handleClose} severity="error" sx={{
                width: '100%',
                marginBottom: (sm) && '77.5vh'

            }}>
                    { errorAlert.title }
                </Alert>
            </Snackbar>
    );
};