import React from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router';

import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import EmailIcon from '@mui/icons-material/Email';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';
import MailOutlineIcon from '@mui/icons-material/MailOutline';

export const Footer = () => {

    const navigate = useNavigate();

    const { filteredProducts, productRows } = useSelector(state => state.product);


    return (
        <Box sx={{
            position: ( (!filteredProducts || filteredProducts?.length === 0) || !productRows ) ? 'absolute' : 'relative',
            bottom: 0,
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: '#000',
            color: '#fff',
            width: '100%',
            height: '30vh',
            zIndex: 999,
            '& #containerSocialMedias': {
                width: '120%',
                marginTop: '2.5vh',
                marginLeft: '-10%',
                display: 'flex',
                justifyContent: 'center',
                
                    '& .MuiSvgIcon-root': {
                        ':hover':{
                        cursor: 'pointer',
                        color: '#7B7D7D'
                        }
                    },
            },
            '& .MuiSvgIcon-root': {
                fontSize: '40px',
                margin: 'auto',
            },

        }}>
            <div>

                <Typography
                    variant='h3'
                    fontSize='30px'
                >
                    CONTACTO
                </Typography>

                <div id='containerSocialMedias'>
                    <WhatsAppIcon onClick={() => window.open('https://api.whatsapp.com/send?phone=542216106943', '_blank')} />
                    <InstagramIcon onClick={() => window.open('https://www.instagram.com/lucass_ojedaa/', '_blank')} />
                    <FacebookIcon onClick={() => window.open('https://www.facebook.com/lucas.ojeda.10888', '_blank')} />
                    <MailOutlineIcon onClick={() => window.open('https://mail.google.com/mail/u/0/?tab=rm&ogbl#inbox?compose=CllgCJZcRPPtpQFjcGrXKdKwprKrkBrdhGDBlfRQCBKCScqLFxmXrtrXjwdnPclVLzSvqKgVpLq', '_blank')} />
                </div>

            </div>
        </Box>
    );
};