import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';

import { Image } from 'cloudinary-react';

import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DeleteIcon from '@mui/icons-material/Delete';
import Typography from '@mui/material/Typography';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import { Container } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import useMediaQuery from '@mui/material/useMediaQuery';
import Badge from '@mui/material/Badge';
import { useTheme } from '@mui/material/styles';


import { userStartDeletedCart } from '../../actions/users';

export const CartMenu = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const { uid, cart: cartProducts } = useSelector(state => state.auth);

    const badgeNumber = cartProducts.length;

    const [anchorCartMenu, setAnchorCartMenu] = useState(null);

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));

    const handleDeleteCart = ({ cart }) => {

        dispatch(userStartDeletedCart({ id: cart._id, name: cart.name, img: cart.img }, uid));
    }


    // Cart menu
    const handleCartMenu = (event) => {

        if (sm) {
            navigate(`/cart`, { replace: true })
        } else {

            setAnchorCartMenu(event.currentTarget);
        }
    };

    const handleCloseCartMenu = () => {
        setAnchorCartMenu(null);
    };

    return (
        <>
            {
                ( location.pathname !== '/cart' )
                &&
                <>
                    <Menu
                        anchorEl={anchorCartMenu}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        sx={{
                            marginTop: (md) ? '4%' : '2.5%',
                            marginLeft: (md) ? '-4%' : '-2.5%',
                            borderRadius: 'none',
                        }}
                        keepMounted
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        open={Boolean(anchorCartMenu)}
                        onClose={handleCloseCartMenu}
                    >
                        <Container sx={{
                            overflowY: 'scroll',
                            maxHeight: '50vh'
                        }}>


                            {
                                cartProducts.map(
                                    (e, i) =>
                                        <MenuItem
                                            key={e._id}
                                            sx={{
                                                '& #deleteIconFromMenuCart': {
                                                    zIndex: 100,
                                                    visibility: 'hidden',
                                                },
                                                ':hover': {
                                                    '& #deleteIconFromMenuCart': {
                                                        visibility: 'visible'
                                                    }
                                                }
                                            }}
                                        >
                                            <Container
                                                sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                    alignItems: 'center',
                                                    '& #productCartImage': {
                                                        maxWidth: '5ch',
                                                        maxHeight: '5ch',
                                                    },
                                                }}
                                                onClick={() => {
                                                    navigate(`/product/${e._id}`, { replace: true })
                                                    handleCloseCartMenu()
                                                }}
                                            >
                                                <Image
                                                    id='productCartImage'
                                                    cloudName="the-kings-company"
                                                    publicId={e.img[0].imageUrl}
                                                    alt='Product' />
                                                {e.name.split(" ")[0]} {e.name.split(" ")[1]}...
                                            </Container>
                                            <IconButton
                                                id='deleteIconFromMenuCart'
                                                size="large"
                                                aria-label="account of current user"
                                                aria-controls="menu-appbar"
                                                aria-haspopup="true"
                                                onClick={() => handleDeleteCart({ cart: e })}
                                                color="inherit"
                                            >
                                                <CloseIcon />
                                            </IconButton>

                                        </MenuItem>
                                )
                            }
                        </Container>

                        {
                            (cartProducts.length)
                                ?
                                <MenuItem sx={{
                                    backgroundColor: '#F7DC6F',
                                    textAlign: 'center',
                                    borderRadius: '5px',
                                    margin: '0 2.5% 0 2.5%',
                                    ':hover': {
                                        backgroundColor: '#F29102',
                                        color: '#fff'
                                    },
                                }}
                                    onClick={() => navigate(`/cart`, { replace: true })}
                                >
                                    <Typography>
                                        Ir al carrito

                                    </Typography>
                                </MenuItem>
                                :
                                <MenuItem sx={{
                                    textAlign: 'center',
                                }}>
                                    <Typography>
                                        An no hay ningn producto en el carrito!
                                    </Typography>
                                </MenuItem>
                        }
                    </Menu>

                    <IconButton
                        size="large"
                        edge="end"
                        color="inherit"

                        sx={{
                            margin: 'auto',
                            mr: (!sm) && 0
                        }}
                        onClick={handleCartMenu}
                    >
                        <Badge
                            badgeContent={badgeNumber}
                            color="warning"
                        >
                            <ShoppingCartIcon sx={{
                            }} />
                        </Badge>
                    </IconButton>
                </>
            }
        </>
    );
};