import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';

import queryString from 'query-string';

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import AccountCircle from '@mui/icons-material/AccountCircle';
import DashboardIcon from '@mui/icons-material/Dashboard';
import LogoutIcon from '@mui/icons-material/Logout';
import MenuIcon from '@mui/icons-material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { startLogout } from '../../actions/auth';
import { clearFilteredProducts, searchProducts } from '../../actions/products';
import { CategoriesBar } from './CategoriesBar';
import { CartMenu } from './CartMenu';
import { NotificationsMenu } from './NotificationsMenu';
import { DialogLogout } from '../dashboard/alerts/DialogLogout';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  marginRight: 0,
  width: '100%',
  [theme.breakpoints.up('lg')]: {
    marginLeft: theme.spacing('5%'),
    width: 'auto',
  },
  [theme.breakpoints.down('lg')]: {
    marginLeft: theme.spacing('12.5%'),
    width: 'auto',
  },
  [theme.breakpoints.between('sm', 'md')]: {
    marginLeft: theme.spacing('12.5%'),
    width: 'auto',
  },
  [theme.breakpoints.down('sm')]: {
    marginLeft: theme.spacing('0%'),
    width: '100%',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      width: '42.5vw',
      '&:focus': {
        width: '45vw',
      },
    },
    [theme.breakpoints.between('sm', 'lg')]: {
      width: '28vw',
      '&:focus': {
        width: '28.5vw',
      },
    },
    [theme.breakpoints.down('md')]: {
      width: '40vw',
    },
  }
}));


export const LargueLoggedBar = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  const { q = '' } = queryString.parse(location.search);
  const { c = '' } = queryString.parse(location.search);

  const { uid, role, name } = useSelector(state => state.auth);

  const [searchText, setSearchText] = useState(q);
  const [anchorEl, setAnchorEl] = useState(null);
  const [dialogLogoutOpen, setDialogLogoutOpen] = useState(false);


  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));

  // Search
  const handleInputChange = (e) => {
    e.preventDefault();
    setSearchText(e.target.value)
  }

  const handleSearch = (e) => {
    e.preventDefault();

    if (searchText !== '') {

      navigate(`?q=${searchText}`)

      dispatch(searchProducts(searchText))
    }


  }

  // Normal menu
  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleLogout = () => {
    setDialogLogoutOpen(true);
    handleClose();
  }

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <Box sx={{
      width: '100%',
      margin: 0,
      padding: 0,
      overflow: 'hidden',
    }}>
      <DialogLogout
        dialogLogoutOpen={dialogLogoutOpen}
        setDialogLogoutOpen={setDialogLogoutOpen}
      />
      <AppBar
        sx={{
          position: 'fixed',
          width: '100%',
          height: '10vh',
          margin: 0,
          padding: 0,
          backgroundColor: '#154360'
        }}>
        <Toolbar sx={{
          display: 'flex',
          justifyContent: 'space-between'
        }}>


          <IconButton
            color="inherit"
            edge="start"
            onClick={() => navigate('/?page=1', { replace: true })}
            sx={{
              marginRight: '36px',
            }}
          >
            <HomeIcon />

          </IconButton>
          {
            (role !== 'USER_ROLE')
            &&
            <>
              {
                (!md)
                  ?
                  <Typography
                    variant="p"
                    component="div"
                    onClick={() => navigate('/dashboard', { replace: true })}
                    sx={{
                      cursor: 'pointer',
                      minWidth: '13%'
                    }}>

                    Panel de control

                  </Typography>
                  :
                  <DashboardIcon
                    onClick={() => navigate('/dashboard', { replace: true })}
                    sx={{
                      cursor: 'pointer',
                    }}
                  />
              }
            </>
          }

          <Search>

            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>

            <form onSubmit={handleSearch}>
              <StyledInputBase
                onChange={handleInputChange}
                type='text'
                name='searchText'
                autoComplete='off'
                value={searchText}
              />
            </form>

            {
              (searchText !== '')
              &&
              <IconButton
                sx={{
                  position: 'absolute',
                  top: '0%',
                  left: '90%'
                }}
                onClick={() => setSearchText('')}
                color="inherit"
              >
                <CloseIcon />
              </IconButton>
            }

          </Search>


          <CartMenu />

          <NotificationsMenu />

          <div>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              sx={{
                ml: 1
              }}
              onClick={handleMenu}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              sx={{
                marginTop: (md) ? '4%' : '2.5%',
                marginLeft: (md) ? '-4%' : '-2.5%',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>My perfil</MenuItem>
              <MenuItem onClick={handleLogout}>Cerrar sesin</MenuItem>
            </Menu>
          </div>
          {
            (!md)
            &&
            <Typography
              edge="end"
              variant="p"
              component="div"
              sx={{ textAlign: 'end', minWidth: '10%' }}>
              {name}
            </Typography>
          }

        </Toolbar>
      </AppBar>

      <CategoriesBar
        setSearchText={setSearchText}
      />

    </Box >
  );
};