import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    useLocation,
    useNavigate
} from "react-router-dom";


import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import Fab from '@mui/material/Fab';
import Box from '@mui/material/Box';
import DashboardIcon from '@mui/icons-material/Dashboard';
import TrendingUpIcon from '@mui/icons-material/TrendingUp';
import InventoryIcon from '@mui/icons-material/Inventory';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import GroupIcon from '@mui/icons-material/Group';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import HomeIcon from '@mui/icons-material/Home';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import IconButton from '@mui/material/IconButton';
import AutoDeleteIcon from '@mui/icons-material/AutoDelete';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUp';

import { uiOpenProductModal, uiOpenProductModalEdit, uiOpenUserModalAdd, uiOpenUserModalEdit, uiOpenDialogDelete } from '../../../actions/ui';
import { productBinStartEnable, userBinStartEnable } from '../../../actions/Bin';
import { Container } from '@mui/material';


export const NavigationMenuMovil = () => {

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { pathname } = useLocation();


    const { activeUser } = useSelector(state => state.users);
    const { activeProduct } = useSelector(state => state.product);

    const { activeBinUser } = useSelector(state => state.bin);
    const { activeBinProduct } = useSelector(state => state.bin);


    const [iconsMenu, setIconsMenu] = useState(false);
    const [iconsBinMenu, setIconsBinMenu] = useState(false);
    const [anchorEl, setAnchorEl] = useState(null);
    const [anchorHome, setAnchorHome] = useState(null);



    const handleAddButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModal())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalAdd())
                break;


            default:
                break;
        }

    }

    const handleEditButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModalEdit())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalEdit())
                break;


            default:
                break;
        }

    }

    const handleDeleteButton = () => {

        switch (pathname) {

            case '/dashboard/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }

    }


    const handleBinEnable = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(productBinStartEnable(activeBinProduct))
                break;

            case '/dashboard/bin/users':
                dispatch(userBinStartEnable(activeBinUser))
                break;


            default:
                break;
        }
    }

    const handleBinDelete = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/bin/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }
    }



    const handleOpenMenuBin = Boolean(anchorEl);
    const handleOpenMenuHome = Boolean(anchorHome);

    const handleClick = (event) => {

        if (!!anchorEl) {
            setAnchorEl(null);
        } else {
            setAnchorEl(event.currentTarget);
        }
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleClickHome = (event) => {

        if (!!anchorHome) {
            setAnchorHome(null);
        } else {
            setAnchorHome(event.currentTarget);
        }
    };

    const handleCloseHome = () => {
        setAnchorHome(null);
    };

    return (
        <>
            <BottomNavigation sx={{
                position: 'fixed',
                bottom: 0,
                width: '100vw',
                borderTop: '1px solid #B2B6B5',
                zIndex: (theme) => theme.zIndex.drawer + 1

            }} value={pathname}>


                <BottomNavigationAction
                    label="Papelera"
                    value={
                        (pathname === "/dashboard/bin/users")
                            ?
                            "/dashboard/bin/users"
                            :
                            "/dashboard/bin/products"
                    }
                    icon={<AutoDeleteIcon />}
                    onClick={() => (iconsBinMenu) ? setIconsBinMenu(false) : setIconsBinMenu(true)}
                />


                <BottomNavigationAction
                    label="Sales"
                    value="/dashboard/sales"
                    icon={<TrendingUpIcon />}
                    onClick={() => navigate('/dashboard/sales', { replace: true })}
                />

                <BottomNavigationAction
                    label="Dashboard"
                    value="/dashboard"
                    icon={<DashboardIcon />}
                    onClick={() => navigate('/dashboard', { replace: true })}
                />


                <BottomNavigationAction
                    label="Inicio"
                    value="/"
                    icon={<HomeIcon />}
                    onClick={() => navigate('/', { replace: true })}
                />


                <BottomNavigationAction
                    label="Menu"
                    value=""
                    icon={<MoreVertIcon />}
                    onClick={() => (iconsMenu) ? setIconsMenu(false) : setIconsMenu(true)}
                />


                {
                    (iconsMenu)
                    &&
                    <Box sx={{
                        position: 'absolute',
                        right: 0,
                        bottom: '100%',
                        backgroundColor: '#fff',
                        width: '65px',
                        height: 'auto',
                        display: 'flex',
                        flexWrap: 'wrap',
                        justifyContent: 'center',
                        borderTop: '1px solid gray',
                        borderLeft: '1px solid gray',
                        borderRadius: '2.5px 0 0 0',
                        zIndex: 100,
                        '& .MuiSvgIcon-root': {
                        }
                    }}>

                        <IconButton aria-label="Usuarios" onClick={() => navigate('/dashboard/users', { replace: true })}>
                            <GroupIcon />
                        </IconButton>


                        <IconButton aria-label="Registros" onClick={() => navigate('/dashboard/regist', { replace: true })}>
                            <LibraryBooksIcon />
                        </IconButton>

                        <IconButton aria-label="Productos" onClick={() => navigate('/dashboard/products', { replace: true })}>
                            <InventoryIcon />
                        </IconButton>

                    </Box>
                }

                {
                    (iconsBinMenu)
                    &&
                    <Box sx={{
                        position: 'absolute',
                        left: 0,
                        bottom: '100%',
                        backgroundColor: '#fff',
                        width: '65px',
                        height: 'auto',
                        display: 'flex',
                        flexWrap: 'wrap',
                        justifyContent: 'center',
                        borderTop: '1px solid gray',
                        borderRight: '1px solid gray',
                        borderRadius: '0 2.5px 0 0',
                        zIndex: 100,
                        '& .MuiSvgIcon-root': {
                        }
                    }}>

                        <IconButton aria-label="Usuarios" onClick={() => navigate('/dashboard/bin/users', { replace: true })}>
                            <GroupIcon />
                        </IconButton>

                        <IconButton aria-label="Productos" onClick={() => navigate('/dashboard/bin/products', { replace: true })}>
                            <InventoryIcon />
                        </IconButton>

                    </Box>
                }

                {/* <Box sx={{
                    position: 'absolute',
                    right: 0,
                    bottom: '20vh',
                    width: '60px',
                    display: 'flex',
                    flexWrap: 'wrap',
                    '& .MuiFab-root': {
                        marginTop: '2vh',
                        marginRight: '1vh'
                    }
                }}>


                    {
                        (pathname === '/dashboard/products' || pathname === '/dashboard/users')
                        &&
                        <>
                            {
                                (!!activeUser || !!activeProduct)
                                &&
                                <>
                                    <Fab onClick={handleDeleteButton}>
                                        <DeleteIcon />
                                    </Fab>


                                    <Fab color="secondary" aria-label="Editar" onClick={handleEditButton}>
                                        <EditIcon />
                                    </Fab>
                                </>
                            }
                            <Fab color="primary" aria-label="Crear" onClick={handleAddButton}>
                                <AddIcon />
                            </Fab>
                        </>
                    }

                    {
                        (pathname === '/dashboard/bin/products' || pathname === '/dashboard/bin/users')
                        &&
                        <>
                            {
                                (!!activeBinProduct || !!activeBinUser)
                                &&
                                <>
                                    <Fab color="secondary" aria-label="Editar" onClick={handleBinDelete}>
                                        <DeleteForeverIcon />
                                    </Fab >

                                    <Fab onClick={handleBinEnable}>
                                        <ArrowCircleUpIcon />
                                    </Fab>

                                </>
                            }
                        </>
                    }

                </Box> */}


            </BottomNavigation>
        </>
    );
}
