import React from 'react';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router';
import { useDispatch } from 'react-redux';

import { GoogleLogin } from 'react-google-login';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Container from '@mui/material/Container';
import HomeIcon from '@mui/icons-material/Home';
import IconButton from '@mui/material/IconButton';
import { Button, Divider, Typography } from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import google_logo from '../../assets/google_logo.png';

import { startGoogleLogin, StartLogin, startRegister } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';


export const RegisterScreen = () => {

    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [formRegisterValues, handleRegisterInputChange] = useForm({
        rName: '',
        rEmail: '',
        rPassword: ''

    });
    const { rName, rEmail, rPassword } = formRegisterValues;

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));


    const handleRegister = (e) => {
        e.preventDefault();

        dispatch(startRegister(rName, rEmail, rPassword));

    }

    const handleModLogin = (e) => {
        e.preventDefault();

        dispatch(StartLogin('moderator_test@test.com', '1234567'));

    }
    const handleUserLogin = (e) => {
        e.preventDefault();

        dispatch(StartLogin('user_test@test.com', '1234567'));

    }
    const handleAdminLogin = (e) => {
        e.preventDefault();

        dispatch(StartLogin('admin_test@test.com', '123456'));

    }

    const handleGoogleLogin = (response) => {

        const { tokenId } = response;

        dispatch(startGoogleLogin(tokenId));
    }


    return (

        <Container sx={{

        }} maxWidth="sm">
            <IconButton
                size="large"
                edge="start"
                aria-label="home"
                onClick={() => navigate('/?page=1', { replace: true })}
                sx={{
                    position: 'absolute',
                    color: 'gray',
                    left: '1%',
                    top: (sm) ? -5 : 0,
                    zIndex: 100
                }}
            >
                <HomeIcon sx={{ fontSize: '30px', }} />
            </IconButton>
            <Box
                component="form"
                sx={{
                    position: (sm) && 'absolute',
                    width: (sm) ? '100%' : 'none',
                    marginTop: (!sm) && '5vh',
                    left: (sm) && 0,
                    height: (sm) ? '80%' : '75vh',

                    textAlign: 'center',
                    bgcolor: '#fff',
                    borderRadius: '2px',
                    '& #title': {
                        fontSize: (sm) ? '18px' : '25px',
                        paddingTop: '2%',
                        borderBottom: '1px solid #B2B6B5',
                        marginLeft: '4%',
                        marginRight: '4%',
                        marginBottom: '5%',
                        paddingBottom: '2.5%',
                        visibility: 'visible'
                    },
                    '& #submitButton': {
                        width: '25%',
                        minWidth: '150px',
                        margin: 'auto',
                        marginLeft: '2%',
                        marginBottom: (sm) ? '10%' : '5%',
                    },
                    '& #container': {

                        marginTop: '7.5%',
                        marginBottom: '2.5%',

                    },
                    '& .MuiTextField-root': {
                        width: (sm) ? '85%' : '60%',
                        margin: 'auto',
                        marginTop: (sm) && '10%',

                        display: 'flex',
                        marginBottom: (sm) ? '7.5%' : '2.5%',
                    },
                    '& .google-button': {
                        width: (sm) ? '60%' : '40%',
                        display: 'block',
                        margin: 'auto',
                        height: (sm) ? '7vh' : '7vh',

                        minWidth: '160px',
                        backgroundColor: '#fff',
                        color: '#737373',
                        margin: 'auto',
                        borderWidth: 0,
                        borderRadius: '2px',
                        whiteSpace: 'nowrap',
                        boxShadow: '1px 1px 1px 1px rgba(0,0,0,0.25)',
                        transitionProperty: 'background-color box-shadow',
                        transitionDuration: '150ms',
                        transitionTimingFunction: 'ease-in-out',
                        padding: 0,
                        transform: 'scale(1.4)',
                        ':hover': {
                            boxShadow: '1px 4px 5px 1px rgba(0,0,0,0.2)',
                            outline: 'none',
                            transform: 'scale(1.4) skewX(-0.4deg)',
                            cursor: 'pointer',
                        },
                        ':active': {
                            outline: 'none',
                            boxShadow: '1px 4px 5px 1px rgba(0,0,0,0.3)',
                            transitionDuration: '10ms',
                        }
                    },
                    '& .google-button__icon': {
                        display: 'inline-block',
                        verticalAlign: 'middle',
                        margin: '0px 0 8px 8px',
                        marginTop: '5px',
                        width: '18px',
                        height: '18px',
                        boxSizing: 'border-box',
                    },
                    '& .Link': {
                        display: 'block',
                        marginTop: (sm) ? '10%' : '5%',
                    },
                }}
                noValidate
                autoComplete="off"
            >

                <Typography fontSize={20} id='title' variant="body2" color="text.primary">
                    Registrarse
                </Typography>


                <Container id='container'>

                    <div>
                        <TextField
                            required
                            id='name'
                            variant='outlined'
                            label="Nombre"
                            name='rName'
                            value={rName}
                            onChange={handleRegisterInputChange}
                        />

                        <TextField
                            required
                            variant='outlined'
                            label="Email"
                            name='rEmail'
                            value={rEmail}
                            onChange={handleRegisterInputChange}
                        />

                        <TextField
                            required
                            variant='outlined'
                            label="Contrasea"
                            name='rPassword'
                            value={rPassword}
                            onChange={handleRegisterInputChange}
                        />

                    </div>
                </Container>

                <Container>
                    <Button
                        type="submit"
                        id="submitButton"
                        variant="outlined"
                        onClick={handleRegister}
                    >Registrarme</Button>
                </Container>


                <GoogleLogin
                    clientId="263099325228-55asn431srakct5pegne7a7go6hjctq6.apps.googleusercontent.com"
                    render={renderProps => (
                        <button className='google-button' onClick={renderProps.onClick} disabled={renderProps.disabled}>Ingresar con Google<img className='google-button__icon' alt='google_logo' src={google_logo} /></button>
                    )}
                    buttonText="Login"
                    onSuccess={handleGoogleLogin}
                    onFailure={handleGoogleLogin}
                    cookiePolicy={'single_host_origin'}
                />

                <Link className='Link' to="/login"> Ya tienes una cuenta ? - Ingresar </Link>

            </Box>
            <Container sx={{
                position: (sm) && 'absolute',
                top: (sm) && '80%',
                left: (sm) && 0,
                height: (sm) ? '20%' : '15vh',
                borderBottomLeftRadius: (!sm) && '5px',
                borderBottomRightRadius: (!sm) && '5px',


                backgroundColor: '#1ABC9C',
                width: '100%',
                margin: 0,
                padding: 0,
                h1: {
                    display: 'block',
                    color: '#fff',
                    height: '5vh',
                    width: '100%',
                    paddingTop: '1.25vh',
                    marginBottom: '1.25vh',
                    textAlign: 'center',
                    fontSize: (sm) ? '16px' : '20px',
                    visibility: 'visible'
                },
                '& #user': {
                    ':hover': {
                        backgroundColor: '#76448A',
                        transition: 'all 0.5s ease-out'
                    }
                },
                '& #moderator': {
                    ':hover': {
                        backgroundColor: '#000080',
                        transition: 'all 0.5s ease-out'
                    }
                },
                '& #admin': {
                    ':hover': {
                        backgroundColor: '#D61E53',
                        transition: 'all 0.5s ease-out'
                    }
                },
                '& .button': {
                    marginTop: (sm) && '2.5%',
                    fontSize: (sm) && '12px',
                    borderRadius: '2.5px',

                    height: '5vh',
                    width: '33%',
                    color: '#fff',
                },

            }}>
                <h1>Ingreso rapido (demostracin de roles)</h1>
                <Button className='button' id="user" onClick={handleUserLogin}>usuario</Button>
                <Button className='button' id="moderator" onClick={handleModLogin}>moderador</Button>
                <Button className='button' id="admin" onClick={handleAdminLogin}>admin</Button>
            </Container>
        </Container>

    );
};