import React, { useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack'

import queryString from 'query-string';

import { CardsMainHome } from '../dashboard/mainCards/CardsMainHome';
import { Footer } from '../ui/Footer';
import { useDispatch, useSelector } from 'react-redux';
import { clearFilteredProducts, searchProducts } from '../../actions/products';
import { useLocation, useNavigate } from 'react-router';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';


export const Home = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const { uid } = useSelector(state => state.auth);
    const { filteredProducts } = useSelector(state => state.product);
    const { productRows } = useSelector(state => state.product);

    var type = '';

    const { q = '' } = queryString.parse(location.search);
    const { c = '' } = queryString.parse(location.search);
    let { page: pagePath = 1 } = queryString.parse(location.search);


    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));
    const xl = useMediaQuery(theme.breakpoints.down('xl'));
    const lg = useMediaQuery(theme.breakpoints.down('lg'));


    const handlePaginationChange = ( e, value) => {

        console.log(value)

        let url = '';

        if (c !== '') {

            url += `?c=${c}`
        } else {

            if (q !== '') {

                url += `?q=${q}`
            }
        }

        (!url.includes('?')) ? url += `?page=${value}` : url += `&page=${value}`;

        navigate(url)
    }


    return (
        <>

            {
                (productRows)
                    ?
                    <>
                        <Container sx={{
                            '& .MuiPagination-root': {
                                margin: 'auto',
                                marginBottom: '2.5%'
                            },

                        }}>
                            <Box sx={{
                                display: 'flex',
                                flexWrap: 'wrap',
                                justifyContent: 'space-around',
                                marginTop: (sm) ? '20%' : '17.5vh',
                            }}>

                                <CardsMainHome />

                            </Box>
                            {
                                (filteredProducts && filteredProducts?.length !== 0)
                                &&
                                <Stack
                                    sx={{
                                        marginTop: '2.5%'
                                    }}
                                    spacing={2}>
                                    <Pagination
                                        aria-current='page'
                                        defaultPage={1}
                                        page={Number(pagePath)}
                                        count={Math.ceil(parseInt(productRows?.length) / 8)}
                                        onChange={handlePaginationChange}

                                    />
                                </Stack>
                            }

                        </Container>
                        <Footer />
                    </>
                    :
                    <>
                    </>
            }
        </>
    );
};