import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";
import { startLoadBinProducts } from "./Bin";
import { uiCloseProgressBackdrop, uiOpenErrorAlert, uiOpenProgressBackdrop, uiOpenSuccessAlert } from "./ui";
 
  
export const activeProduct = ( product ) => ({
    type: types.activeProduct,
    payload: product
})

export const clearActiveProduct = () => ({
    type: types.clearActiveProduct
})

export const clearFilteredProducts = () => ({
    type: types.clearFilteredProducts
})

export const searchProducts = (q = '', page = 1) => {
    return async( dispatch ) => {

        const term = (q !== '') ? q : 'home';

        const limit = page * 8;
        const since = (page * 8) - 8;
        try {
            const resp = await fetchSinToken( `search/products/${term}?limit=${limit}&since=${since}` );
            const body = await resp.json(); 


            if( body.msg === 'OK' ) {    

                console.log('Filtered products', body);

                const filteredProducts = body.results;

                console.log(filteredProducts)

                dispatch( loadFilteredProducts(filteredProducts) );

                window.scroll(0, 0);

            } else {
                console.log(body.msg);
            }
            

        } catch (error) {
            console.log(error);
        }
    }
}

const loadFilteredProducts = (filteredProducts) => ({
    type: types.loadFilteredProducts,
    payload: filteredProducts
})

export const startLoadProducts = () => {
    return async( dispatch ) => {


        try {
            const resp = await fetchSinToken( 'products' );
            const body = await resp.json();


            if( body.products ) {    

                const productRows = body.products;

                console.log(productRows)

                dispatch( loadProducts(productRows) );

            } else {
                console.log(body.msg);
            }
            

        } catch (error) {
            console.log(error);
        }
    }
}

const loadProducts = (productRows) => ({
    type: types.loadProducts,
    payload: productRows
})

export const productStartAddNew = (product) => {

    return async (dispatch) => {



        try {


            const { category } = product;

            const productNew = {...product, category: category.name};

            console.log(productNew)

            const resp = await fetchConToken('products', productNew, 'POST');
            const body = await resp.json();

            console.log(body);

            if (body.msg === "OK") { 

                dispatch( uiCloseProgressBackdrop() );

                dispatch(startLoadProducts(body.product));

                dispatch( uiOpenSuccessAlert('El producto fue creado exitosamente!') );

            } else {
                dispatch( uiCloseProgressBackdrop() );
                dispatch(uiOpenErrorAlert('Error al intentar crear el producto! Hable con el administrador'));
                console.log(body.msg);
            }


        } catch (error) {
            dispatch( uiCloseProgressBackdrop() );
            dispatch(uiOpenErrorAlert('Error al intentar crear el producto! Hable con el administrador'));
            console.log(error);
        }
    }
}

const productAddNew = (product) => ({
    type: types.productAddNew,
    payload: product
});

export const productStartUpdated = (product) => {
    return async(dispatch) => {

        try {


            const { category } = product

            const productNew = {...product, category: category.name}

            const resp = await fetchConToken( `products/${product._id}`, {product: productNew} , 'PUT' );
            const body = await resp.json();

            console.log(body);


            if( body.msg === 'OK' ){

                dispatch( clearActiveProduct() );

                dispatch( uiCloseProgressBackdrop() );

                dispatch( startLoadProducts() );


                dispatch( uiOpenSuccessAlert('El producto fue actualizado exitosamente!') );


            } else {
                dispatch( uiCloseProgressBackdrop() );
                dispatch(uiOpenErrorAlert('Error al intentar actualizar el producto! Hable con el administrador'));
                console.log(body.msg);
            }


        } catch (error) {
            dispatch( uiCloseProgressBackdrop() );
            dispatch(uiOpenErrorAlert('Error al intentar actualizar el producto! Hable con el administrador'));
            console.log(error);
        }

    }
}

export const productStartDeleted = (product) => {
    return async(dispatch) => {

        try {

            dispatch( uiOpenProgressBackdrop() );


            const resp = await fetchConToken( `products/${product._id}`,{}, 'DELETE' );
            const body = await resp.json();

            console.log(body);


            if( body.msg === "OK" ){

                dispatch( clearActiveProduct() );

                dispatch( uiCloseProgressBackdrop() );

                dispatch( startLoadProducts() );
                dispatch( startLoadBinProducts() );

                dispatch( uiOpenSuccessAlert('El producto fue eliminado exitosamente!') );


            } else {
                dispatch( uiCloseProgressBackdrop() );
                dispatch(uiOpenErrorAlert('Error al intentar eliminar el producto! Hable con el administrador'));
                console.log(body.msg);
            }


        } catch (error) {
            dispatch( uiCloseProgressBackdrop() );
            dispatch(uiOpenErrorAlert('Error al intentar eliminar el producto! Hable con el administrador'));
            console.log(error);
        }

    }
}

export const productLogout = () => ({
    type: types.productLogout
})  