import { types } from "../types/types";

const initialState = {
    activeProduct: null,
}


export const productReducer = (state = initialState, action) => {

    switch (action.type) {
        case types.activeProduct:
            return {
                ...state,
                activeProduct: { ...action.payload }
            }

        case types.clearActiveProduct:
            return {
                ...state,
                activeProduct: null
            }
            
        case types.clearFilteredProducts:
            return {
                ...state,
                filteredProducts: undefined
            }
            
        case types.loadProducts:
            return {
                ...state,
                productRows: action.payload
            }

        case types.loadFilteredProducts:
            return {
                ...state,
                filteredProducts: action.payload
            }

        case types.productAddNew:
            return {
                ...state,
                productRows: [
                    ...state.productRows,
                    action.payload
                ]
            }


        case types.productUpdated:
            return {
                ...state,
                productRows: state.productRows.map(
                    e => (e.id === action.payload.id) ? action.payload : e
                )
            }

        case types.productDeleted:
            return {
                ...state,
                productRows: state.productRows.filter(
                    e => (e.id !== state.activeProduct.id)
                ),
                activeProduct: null
            }

        case types.productLogout:
            return {
                ...initialState
            }


        default:
            return state;
    }
}