import React from 'react';
import { useLocation } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';

import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { uiCloseDialogDelete } from '../../../actions/ui';
import { productStartDeleted } from '../../../actions/products';
import { userStartDeleted } from '../../../actions/users';
import { startDeleteRecords } from '../../../actions/records';
import { productBinStartDeleted, userBinStartDeleted } from '../../../actions/Bin';

export const DialogDelete = () => {

    const dispatch = useDispatch();
    const { pathname } = useLocation();


    const { dialogDelete } = useSelector(state => state.ui);

    const { activeProduct } = useSelector(state => state.product);
    const { activeUser } = useSelector(state => state.users);

    const { activeBinProduct } = useSelector(state => state.bin);
    const { activeBinUser } = useSelector(state => state.bin);



    const handleClose = () => {
        dispatch(uiCloseDialogDelete());
    };

    const handleDelete = () => {
        dispatch(uiCloseDialogDelete());
        switch (pathname) {

            case '/dashboard/products':
                dispatch(productStartDeleted(activeProduct))
                break;

            case '/dashboard/users':
                dispatch(userStartDeleted(activeUser))
                break;

            case '/dashboard/bin/products':
                dispatch(productBinStartDeleted(activeBinProduct))
                break;

            case '/dashboard/bin/users':
                dispatch(userBinStartDeleted(activeBinUser))
                break;

            case '/dashboard/regist':
                dispatch(startDeleteRecords())
                break;


            default:
                dispatch(productStartDeleted(activeProduct))
                break;
        }

    }

    return (
        <div>
            <Dialog
                open={dialogDelete}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">
                    {"Advertencia!"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Est seguro que desea realizar esta accin?
                        Podria ser irreversible
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleClose}>
                        No
                    </Button>
                    <Button autoFocus onClick={handleDelete}>
                        Si
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
