import React from 'react';
import { useLocation } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';

import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { salesStartAddNew } from '../../../actions/sales';

export const DialogBuy = ({dialogBuyOpen, setDialogBuyOpen, cart}) => {

    const dispatch = useDispatch();


    const handleBuy = () => {

        dispatch(salesStartAddNew(cart))
        setDialogBuyOpen(false);
    };

    const handleClose = () => {
        setDialogBuyOpen(false);
    };


    return (
        <div>
            <Dialog
                open={dialogBuyOpen}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">
                    {"Por favor confirme su compra!"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Est a solo un paso de terminar su compra!
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleClose}>
                        Cancelar
                    </Button>
                    <Button autoFocus onClick={handleBuy}>
                        Comprar
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
