import React from 'react';
import { Route, Routes, useLocation } from 'react-router';

import { ProductsDashboard } from './ProductsDashboard';
import { RecordDashboard } from './RecordDashboard';
import { UsersDashboard } from './UsersDashboard';
import { BinProductsDashboard } from './BinProductsDashboard';
import { BinUsersDashboard } from './BinUsersDashboard';
import { SalesDashboard } from './SalesDashboard';

import { CardProducts } from './mainCards/CardProducts';


export const Dashboard = () => {

    const { pathname } = useLocation();

    return (
        <>
            

            <Routes>
                <Route path="products" element={<ProductsDashboard />} />
                <Route path="users" element={<UsersDashboard />} />
                <Route path="regist" element={<RecordDashboard />} />
                <Route path="bin/products" element={<BinProductsDashboard />} />
                <Route path="bin/users" element={<BinUsersDashboard />} />
                <Route path="sales" element={<SalesDashboard />} />
            </Routes>


            {
                (pathname === '/dashboard')
                &&
                <CardProducts />
            }
        </>

    );
};

