import { fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";


export const searchUsers = (q = '', page = 1) => {
    return async( dispatch ) => {

        const term = (q !== '') ? q : 'home';

        const limit = page * 8;
        const since = (page * 8) - 8;
        try {
            const resp = await fetchSinToken( `search/users/${term}?limit=${limit}&since=${since}` );
            const body = await resp.json(); 


            if( body.msg === 'OK' ) {    

                console.log('Filtered users', body);

                const filteredUsers = body.results;

                console.log(filteredUsers)

                dispatch( loadFilteredUsers(filteredUsers) );

                window.scroll(0, 0);

            } else {
                console.log(body.msg);
            }
            

        } catch (error) {
            console.log(error);
        }
    }
}

const loadFilteredUsers = (filteredUsers) => ({
    type: types.loadFilteredUsers,
    payload: filteredUsers
})

export const clearFilteredUsers = () => ({
    type: types.clearFilteredUsers
})

