

export const styles__newTables = (sm, md, lg, xl) => { 
    switch (true) {

        case sm:
            return newTables__smBox();

        case md:
            return newTables__mdBox();

        case lg:
            return newTables__lgBox();

        case xl:
            return newTables__xlBox();

        default:
            break;
    }
}


const newTables__xlBox = () => ({
    '& #tableContainer': {
        width: '90%',
        height: 'auto',
        display: 'flex',
        flexWrap: 'wrap',
        margin: 'auto',
        marginTop: '15vh',
        marginLeft: '8.5vw',
        padding: '0',
        border: '1px solid rgba(1, 1, 1, 0.25)',
    }, 
    '& #containerRows': {
        width: '100%',
        height: '60vh',
        display: 'flex',
        flexWrap: 'wrap',
        margin: 'auto',
        padding: '0',
        overflowY: 'scroll',
    }, 
    '& #rowsDiv': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginBottom: '2px',
        padding: '0 2.5% 0 2.5%',
        width: '100%',
        height: '7.5ch',
        backgroundColor: '#fff',
        ':hover': {
            '& #menuIconDiv': {
                visibility: 'visible',
            },
        }
    },
    '& #headerDiv': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        borderBottom: '1px solid rgba(1, 1, 1, 0.25)',
        overflowX: 'hidden',
        padding: '0 4vw 0 2.5%',
        width: '100%',
        height: '7.5ch',
        backgroundColor: '#2980B9',
        color: '#fff',
        ':hover': {
            '& #menuIconDiv': {
                visibility: 'visible',
            },
        }
    },
    '& #nameItemContainer': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        width: '20ch',
        height: '7.5ch',
    },
    '& #priceItemContainer': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '7.5ch',
        height: '7.5ch',
    },
    '& #quantityItemContainer': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '7.5ch',
        height: '7.5ch',
    },
    '& #categoryItemContainer': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '10ch',
        height: '7.5ch',
    },
    '& #userItemContainer': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '20ch',
        height: '7.5ch',
    },
    '& #menuIconDiv': {
        visibility: 'hidden',
    },
    '& #paginationDiv': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        overflow: 'hidden',
        width: '100%',
        height: '6ch',
        padding: '0 4vw 0 2.5%',
        borderRadius: '0 0 5px 5px',
        backgroundColor: '#fff',
        borderTop: '1px solid rgba(1, 1, 1, 0.25)',
    },
});

