import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { DataGrid } from '@mui/x-data-grid';
import { Box, Container, Typography } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import IconButton from '@mui/material/IconButton';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack'
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import queryString from 'query-string';

import { activeProduct, searchProducts } from '../../../actions/products';
import { prepareProducts } from '../../../helpers/prepareProducts';
import { uiOpenDialogDelete, uiOpenProductModalEdit } from '../../../actions/ui';
import { styles__newTables } from '../../../styles/styles__newTables';
import { useLocation, useNavigate } from 'react-router';


export const NewProductsDashboardTable = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const { productRows, filteredProducts } = useSelector(state => state.product);

    const { q = '' } = queryString.parse(location.search);
    const { c = '' } = queryString.parse(location.search);
    let { page: pagePath = 1 } = queryString.parse(location.search);

    const [anchorEl, setAnchorEl] = useState(null);

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));
    const xl = useMediaQuery(theme.breakpoints.down('xl'));
    const lg = useMediaQuery(theme.breakpoints.down('lg'));


    useEffect(() => {


        (!filteredProducts)

            ? dispatch(searchProducts(q, Number(pagePath)))

            : ((c !== '')

                ? dispatch(searchProducts(c, Number(pagePath)))

                : dispatch(searchProducts(q, Number(pagePath)))
            )

    }, [location.search]);


    //   if (productRows !== undefined) {

    //     var productRowsPrepared = prepareProducts(productRows);
    //   }

    const onRowClick = (event) => {

        const productSelected = (filteredProducts.filter(
            e => (e._id === event.row.id)
        ));


        dispatch(activeProduct(productSelected[0]))
    }

    //   const onRowDoubleClick = () => {
    //     dispatch(uiOpenProductModalEdit());
    //   }

    const handlePaginationChange = (e, value) => {

        console.log(value)

        let url = '';

        if (c !== '') {

            url += `?c=${c}`
        } else {

            if (q !== '') {

                url += `?q=${q}`
            }
        }

        (!url.includes('?')) ? url += `?page=${value}` : url += `&page=${value}`;

        navigate(url)
    }

    const handleEditButton = (e) => {
        e.preventDefault();

        dispatch(uiOpenProductModalEdit())

        handleLogout();
    }

    const handleDeleteButton = (e) => {
        e.preventDefault();

        dispatch(uiOpenDialogDelete())


        handleLogout();


    }

    // Normal menu
    const handleMenu = (e, product) => {
        e.preventDefault();

        dispatch(activeProduct(product));

        setAnchorEl(e.currentTarget);
    };

    const handleLogout = () => {
        handleClose();
    }

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <div>

            <div>
                {
                    (filteredProducts !== undefined)
                        ?
                        <Box
                            sx={styles__newTables(sm, md, lg, xl)}>


                            <Container id='tableContainer'>

                                <div id='headerDiv'>

                                    <div id='nameItemContainer'>
                                        <Typography variant='body2'>
                                            Nombre
                                        </Typography>
                                    </div>

                                    <div id='priceItemContainer'>
                                        <Typography variant='body2'>
                                            Precio
                                        </Typography>
                                    </div>

                                    <div id='quantityItemContainer'>
                                        <Typography variant='body2'>
                                            Cantidad
                                        </Typography>
                                    </div>

                                    <div id='categoryItemContainer'>
                                        <Typography variant='body2'>
                                            Categoria
                                        </Typography>
                                    </div>

                                    <div id='userItemContainer'>
                                        <Typography variant='body2'>
                                            Usuario
                                        </Typography>
                                    </div>

                                    <div id='menuIconDiv'>
                                        <IconButton
                                            size="large"
                                            aria-label="account of current user"
                                            aria-controls="menu-appbar"
                                            aria-haspopup="true"
                                            // onClick={(event) => handleMenu(event, e)}
                                            color="inherit"
                                            id='productMenuIcon'
                                        >
                                            <MoreVertIcon />
                                        </IconButton>
                                        <Menu
                                            anchorEl={anchorEl}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            sx={{
                                                boxShadow: '1px solid #000'
                                            }}
                                            transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            open={Boolean(anchorEl)}
                                            onClose={handleClose}
                                        >
                                            <MenuItem onClick={handleEditButton}>Editar</MenuItem>
                                            <MenuItem onClick={handleDeleteButton}>Eliminar</MenuItem>
                                        </Menu>
                                    </div>
                                </div>

                                <Container id='containerRows'>

                                    {
                                        filteredProducts.map(
                                            e => <div key={e._id} id='rowsDiv'>

                                                <div id='nameItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.name.split(" ")[0]} {e.name.split(" ")[1]}
                                                    </Typography>
                                                </div>

                                                <div id='priceItemContainer'>
                                                    <Typography variant='body2'>
                                                        {`$${new Intl.NumberFormat('es-IN').format(e.price)}`}
                                                    </Typography>
                                                </div>

                                                <div id='quantityItemContainer'>
                                                    <Typography variant='body2'>
                                                        {new Intl.NumberFormat('es-IN').format(e.quantity)}
                                                    </Typography>
                                                </div>

                                                <div id='categoryItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.category.name}
                                                    </Typography>
                                                </div>

                                                <div id='userItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.user.name}
                                                    </Typography>
                                                </div>

                                                <div id='menuIconDiv'>
                                                    <IconButton
                                                        size="large"
                                                        aria-label="account of current user"
                                                        aria-controls="menu-appbar"
                                                        aria-haspopup="true"
                                                        onClick={(event) => handleMenu(event, e)}
                                                        color="inherit"
                                                        id='productMenuIcon'
                                                    >
                                                        <MoreVertIcon />
                                                    </IconButton>
                                                    <Menu
                                                        anchorEl={anchorEl}
                                                        anchorOrigin={{
                                                            vertical: 'top',
                                                            horizontal: 'right',
                                                        }}
                                                        sx={{
                                                            boxShadow: '1px solid #000'
                                                        }}
                                                        transformOrigin={{
                                                            vertical: 'top',
                                                            horizontal: 'right',
                                                        }}
                                                        open={Boolean(anchorEl)}
                                                        onClose={handleClose}
                                                    >
                                                        <MenuItem onClick={handleEditButton}>Editar</MenuItem>
                                                        <MenuItem onClick={handleDeleteButton}>Eliminar</MenuItem>
                                                    </Menu>
                                                </div>

                                            </div>
                                        )
                                    }

                                </Container>

                                <Container id='paginationDiv'>
                                    {
                                        (filteredProducts && filteredProducts?.length !== 0)
                                        &&
                                        <Stack
                                            spacing={2}>
                                            <Pagination
                                            size='large'
                                                aria-current='page'
                                                defaultPage={1}
                                                page={Number(pagePath)}
                                                count={Math.ceil(parseInt(productRows?.length) / 8)}
                                                onChange={handlePaginationChange}

                                            />
                                        </Stack>
                                    }
                                </Container>

                            </Container>

                        </Box>
                        :
                        <>
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation={false} />
                        </>
                }
            </div>
        </div>
    );
}
