import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';

import queryString from 'query-string';

import { styled, useTheme, alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import MuiAppBar from '@mui/material/AppBar';
import LogoutIcon from '@mui/icons-material/Logout';
import AccountCircle from '@mui/icons-material/AccountCircle';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import DashboardIcon from '@mui/icons-material/Dashboard';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import HomeIcon from '@mui/icons-material/Home';
import ListItemText from '@mui/material/ListItemText';
import InputBase from '@mui/material/InputBase';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import Badge from '@mui/material/Badge';
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';
import { Container } from '@mui/material';

import { searchProducts } from '../../actions/products';
import { startLogout } from '../../actions/auth';
import { CartMenu } from './CartMenu';
import { NotificationsMenu } from './NotificationsMenu';
import { DialogLogout } from '../dashboard/alerts/DialogLogout';


const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
    backgroundColor: '#154360',
    transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
}));

const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    marginRight: 0,
    width: '100%',
    [theme.breakpoints.up('lg')]: {
        marginLeft: theme.spacing('5%'),
        width: 'auto',
    },
    [theme.breakpoints.down('lg')]: {
        marginLeft: theme.spacing('12.5%'),
        width: 'auto',
    },
    [theme.breakpoints.between('sm', 'md')]: {
        marginLeft: theme.spacing('12.5%'),
        width: 'auto',
    },
    [theme.breakpoints.down('sm')]: {
        marginLeft: theme.spacing('0%'),
        width: '100%',
    },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('lg')]: {
            width: '42.5vw',
            '&:focus': {
                width: '45vw',
            },
        },
        [theme.breakpoints.between('sm', 'lg')]: {
            width: '28vw',
            '&:focus': {
                width: '28.5vw',
            },
        },
        [theme.breakpoints.down('md')]: {
            width: '40vw',
        },
    }
}));


export const SmallLoggedDrawerBar = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const { q = '' } = queryString.parse(location.search);
    const { c = '' } = queryString.parse(location.search);

    const { role } = useSelector(state => state.auth);

    const theme = useTheme();

    const [open, setOpen] = useState(false);
    const [searchText, setSearchText] = useState(q);
    const [dialogLogoutOpen, setDialogLogoutOpen] = useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    // Search
    const handleInputChange = (e) => {
        e.preventDefault();
        setSearchText(e.target.value)
    }

    const handleSearch = (e) => {
        e.preventDefault();

        if (searchText !== '') {

            navigate(`?q=${searchText}`)

            dispatch(searchProducts(searchText))
        }

        handleDrawerClose();


    }

    const handleLogout = () => {

        setDialogLogoutOpen(true);
        handleDrawerClose();
    }


    return (
        <Box sx={{ display: 'flex' }}>

            <DialogLogout
                dialogLogoutOpen={dialogLogoutOpen}
                setDialogLogoutOpen={setDialogLogoutOpen}
            />


            <AppBar sx={{
                position: 'fixed',

            }} open={open}>
                <Toolbar sx={{
                    display: 'flex',
                    justifyContent: 'space-between'
                }}>

                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        sx={{ mr: 2, ...(open && { display: 'none' }) }}
                    >
                        <MenuIcon />
                    </IconButton>



                    <CartMenu />

                    <NotificationsMenu />

                </Toolbar>
            </AppBar>

            <Drawer
                sx={{
                    flexShrink: 0,
                    '& .MuiDrawer-paper': {
                        width: '90%',
                        boxSizing: 'border-box',
                    },
                }}
                variant="persistent"
                anchor="left"
                open={open}
            >

                <DrawerHeader>
                    <Search>
                        <SearchIconWrapper>
                            <SearchIcon />
                        </SearchIconWrapper>

                        <form onSubmit={handleSearch}>
                            <StyledInputBase
                                onChange={handleInputChange}
                                type='text'
                                name='searchText'
                                autoComplete='off'
                                value={searchText}
                            />

                            {
                                (searchText !== '')
                                &&
                                <IconButton
                                    sx={{
                                        position: 'absolute',
                                        top: '0%',
                                        left: '90%'
                                    }}
                                    onClick={() => setSearchText('')}
                                    color="inherit"
                                >
                                    <CloseIcon />
                                </IconButton>
                            }

                        </form>
                    </Search>

                    <IconButton onClick={handleDrawerClose}>
                        {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                    </IconButton>
                </DrawerHeader>

                <Divider />

                <List>

                    <ListItem button
                        onClick={() => {
                            handleDrawerClose();
                            navigate('/?page=1', { replace: true })
                        }}
                    >
                        <ListItemIcon>
                            <HomeIcon />
                        </ListItemIcon>
                        <ListItemText primary={'Inicio'} />
                    </ListItem>

                    {
                        (role === 'ADMIN_ROLE' || role === 'MODERATOR_ROLE')
                        &&
                        <ListItem button
                            onClick={() => {
                                handleDrawerClose();
                                navigate('/dashboard', { replace: true })
                            }}
                        >
                            <ListItemIcon>
                                <DashboardIcon />
                            </ListItemIcon>
                            <ListItemText primary={'Panel de control'} />
                        </ListItem>
                    }

                    <ListItem button
                        onClick={handleDrawerClose}
                    >
                        <ListItemIcon>
                            <AccountCircle />
                        </ListItemIcon>
                        <ListItemText primary={'My perfil'} />
                    </ListItem>

                    <ListItem button
                        onClick={() => {
                            handleDrawerClose();
                            handleLogout();
                        }}
                    >
                        <ListItemIcon>
                            <LogoutIcon />
                        </ListItemIcon>
                        <ListItemText primary={'Cerrar sesin'} />
                    </ListItem>

                </List>


            </Drawer>
        </Box>
    );
}
