import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from 'react-router';

import queryString from 'query-string';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { LargueLoggedBar } from './LargueLoggedBar';
import { SmallLoggedDrawerBar } from './SmallLoggedDrawerBar';

import { searchProducts } from '../../actions/products';


export const NavbarLogged = () => {

  const dispatch = useDispatch();
  const location = useLocation();

  const { filteredProducts } = useSelector(state => state.product);

  const { q = '' } = queryString.parse(location.search);
  const { c = '' } = queryString.parse(location.search);
  let { page: pagePath = 1 } = queryString.parse(location.search);

  useEffect(() => {


    (!filteredProducts)

      ? dispatch(searchProducts(q, Number(pagePath)))

      : ((c !== '')

        ? dispatch(searchProducts(c, Number(pagePath)))

        : dispatch(searchProducts(q, Number(pagePath)))
      )

  }, [location.search]);


  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <>
      {
        (sm)
          ?
          <SmallLoggedDrawerBar />
          :
          <LargueLoggedBar />
      }
    </>
  );
}

