import { types } from "../types/types";

const initialState = {
    filteredUsers: undefined,
}


export const searchReducer = (state = initialState, action) => {

    switch (action.type) {

        case types.loadFilteredUsers:
            return {
                ...state,
                filteredUsers: action.payload
            }

        case types.clearFilteredUsers:
            return {
                ...state,
                filteredUsers: undefined
            }

        case types.loadFilteredRecords:
            return {
                ...state,
                filteredRecords: action.payload
            }

        case types.clearFilteredRecords:
            return {
                ...state,
                filteredRecords: undefined
            }

        case types.loadFilteredSales:
            return {
                ...state,
                filteredSales: action.payload
            }

        case types.clearFilteredSales:
            return {
                ...state,
                filteredSales: undefined
            }

        case types.loadFilteredBinUsers:
            return {
                ...state,
                filteredBinUsers: action.payload
            }

        case types.clearFilteredBinUsers:
            return {
                ...state,
                filteredBinUsers: undefined
            }

        case types.loadFilteredBinProducts:
            return {
                ...state,
                filteredBinProducts: action.payload
            }

        case types.clearFilteredBinProducts:
            return {
                ...state,
                filteredBinProducts: undefined
            }


        default:
            return state;
    }
}