import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    Routes,
    Route,
    BrowserRouter,
    Navigate
} from "react-router-dom";

import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import { LoginScreen } from '../components/auth/LoginScreen';
import { RegisterScreen } from '../components/auth/RegisterScreen';
import { Home } from '../components/Application/Home';
import { Dashboard } from '../components/dashboard/Dashboard';
import { NavbarDashboard } from '../components/dashboard/NavbarDashboard';
import { NavbarLogged } from '../components/ui/NavbarLogged';
import { NavbarUnlogged } from '../components/ui/NavbarUnlogged';

import { startChecking } from '../actions/auth';
import { startLoadUsers } from '../actions/users';
import {
    startLoadBinProducts,
    startLoadBinUsers
} from '../actions/Bin';
import { startLoadRecords } from '../actions/records';
import { startLoadCategories } from '../actions/categories';
import { startLoadProducts } from '../actions/products';

import { PrivateRoute } from './PrivateRoute';
import { PublicRoute } from './PublicRoute';
import { AdminRoute } from './AdminRoute';
import { ProductScreen } from '../components/Application/ProductScreen';

import { EditProductModal } from '../components/dashboard/modals/EditProductModal';
import { ErrorAlert } from '../components/dashboard/alerts/ErrorAlert';
import { SuccessAlert } from '../components/dashboard/alerts/SuccessAlert';
import { DialogDelete } from '../components/dashboard/alerts/DialogDelete';
import { ProgressBackdrop } from '../components/dashboard/progress/ProgressBackdrop';
import { DialogFields } from '../components/dashboard/alerts/DialogFields';
import { CategoriesModal } from '../components/dashboard/modals/CategoriesModal';
import { CartScreen } from '../components/Application/CartScreen';
import { startLoadSales } from '../actions/sales';



export const AppRouter = () => {

    const dispatch = useDispatch();

    const { uid, checking, role } = useSelector(state => state.auth);
    const { productRows } = useSelector(state => state.product);
    const { categories } = useSelector(state => state.categories);

    useEffect(() => {

        dispatch(startChecking());
        dispatch(startLoadProducts())
        dispatch(startLoadCategories())
        dispatch(startLoadSales())

        if (role === 'ADMIN_ROLE') {
            dispatch(startLoadUsers());
            dispatch(startLoadBinUsers());
            dispatch(startLoadBinProducts());
            dispatch(startLoadRecords());
        }

    }, [dispatch]);


    if (checking || !productRows || !categories) {

        return <Backdrop
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={true}
        >
            <CircularProgress color="inherit" size='80px' sx={{ display: 'block' }} />
        </Backdrop>
    }


    return (


        <BrowserRouter>

            <EditProductModal />
            <CategoriesModal />
            <ProgressBackdrop />
            <DialogDelete />
            <DialogFields />
            <ErrorAlert />
            <SuccessAlert />

            <Routes>


                <Route path="login" element={
                    <PublicRoute isAutenticated={!!uid}>
                        <>
                            <LoginScreen />
                        </>
                    </PublicRoute>
                } />

                <Route path="register" element={
                    <PublicRoute isAutenticated={!!uid}>
                        <>
                            <RegisterScreen />
                        </>
                    </PublicRoute>
                } />

                <Route path="/" element={
                    <>

                        {(uid !== null) ? <NavbarLogged /> : <NavbarUnlogged />}
                        <Home />
                    </>

                } />


                <Route path="/cart" element={
                    <PrivateRoute isAutenticated={!!uid}>

                        {(uid !== null) ? <NavbarLogged /> : <NavbarUnlogged />}
                        <CartScreen />

                    </PrivateRoute>
                } />

                <Route path="/product/:id" element={
                    <>
                        {(uid !== null) ? <NavbarLogged /> : <NavbarUnlogged />}
                        <ProductScreen />
                    </>

                } />


                <Route path="dashboard/*" element={
                    <AdminRoute isRole={role}>
                        <>
                            <NavbarDashboard />
                            <Dashboard />
                        </>
                    </AdminRoute>
                } />


                {/* <Route path="/*" element={<Navigate to="/" />} /> */}


            </Routes>

        </BrowserRouter>
    );
};