import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import moment from 'moment';
import 'moment-timezone';
import 'moment/locale/es';


import { DataGrid } from '@mui/x-data-grid';
import { Box, Container, Typography } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import IconButton from '@mui/material/IconButton';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack'
import VisibilityIcon from '@mui/icons-material/Visibility';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import queryString from 'query-string';

import { uiOpenDialogDelete, uiOpenRecordModal } from '../../../actions/ui';
import { styles__newTables } from '../../../styles/styles__newTables';
import { useLocation, useNavigate } from 'react-router';
import { searchRecords } from '../../../actions/search';
import { activeRecord, startLoadRecords } from '../../../actions/records';

moment.locale('es');

export const NewRecordsDashboardTable = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const { filteredRecords } = useSelector(state => state.search);
    const { records } = useSelector(state => state.records);

    const { q = '' } = queryString.parse(location.search);
    const { c = '' } = queryString.parse(location.search);
    let { page: pagePath = 1 } = queryString.parse(location.search);

    const [anchorEl, setAnchorEl] = useState(null);

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));
    const xl = useMediaQuery(theme.breakpoints.down('xl'));
    const lg = useMediaQuery(theme.breakpoints.down('lg'));


    useEffect(() => {

        (!records) && dispatch(startLoadRecords());


        (!filteredRecords)

            ? dispatch(searchRecords(q, Number(pagePath)))

            : dispatch(searchRecords(q, Number(pagePath)))


    }, [location.search]);


    // const onRowClick = (event) => {

    //     const userSelected = (filteredUsers.filter(
    //         e => (e._id === event.row.id)
    //     ));


    //     dispatch(activeUser(userSelected[0]))
    // }

    //   const onRowDoubleClick = () => {
    //     dispatch(uiOpenProductModalEdit());
    //   }

    const handlePaginationChange = (e, value) => {

        console.log(value)

        let url = '';

        if (c !== '') {

            url += `?c=${c}`
        } else {

            if (q !== '') {

                url += `?q=${q}`
            }
        }

        (!url.includes('?')) ? url += `?page=${value}` : url += `&page=${value}`;

        navigate(url)
    }


    // Normal menu
    const handleMenu = (e, record) => {
        e.preventDefault();

        dispatch(activeRecord(record));

        dispatch(uiOpenRecordModal())


    };

    return (
        <div>

            <div>
                {
                    (filteredRecords !== undefined)
                        ?
                        <Box
                            sx={styles__newTables(sm, md, lg, xl)}>


                            <Container id='tableContainer'>

                                <div id='headerDiv'>

                                    <div id='nameItemContainer'>
                                        <Typography variant='body2'>
                                            Nombre
                                        </Typography>
                                    </div>

                                    <div id='priceItemContainer'>
                                        <Typography variant='body2'>
                                            Accin
                                        </Typography>
                                    </div>

                                    <div id='quantityItemContainer'>
                                        <Typography variant='body2'>
                                            Tipo
                                        </Typography>
                                    </div>

                                    <div id='categoryItemContainer'>
                                        <Typography variant='body2'>
                                            Usuario
                                        </Typography>
                                    </div>

                                    <div id='userItemContainer'>
                                        <Typography variant='body2'>
                                            Fecha
                                        </Typography>
                                    </div>

                                    <div id='menuIconDiv'>
                                        <IconButton
                                            size="large"
                                            aria-label="account of current user"
                                            aria-controls="menu-appbar"
                                            aria-haspopup="true"
                                            // onClick={(event) => handleMenu(event, e)}
                                            color="inherit"
                                            id='productMenuIcon'
                                        >
                                            <MoreVertIcon />
                                        </IconButton>
                                        {/* <Menu
                                            anchorEl={anchorEl}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            sx={{
                                                boxShadow: '1px solid #000'
                                            }}
                                            transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            open={Boolean(anchorEl)}
                                            onClose={handleClose}
                                        >
                                            <MenuItem onClick={handleEditButton}>Editar</MenuItem>
                                            <MenuItem onClick={handleDeleteButton}>Eliminar</MenuItem>
                                        </Menu> */}
                                    </div>
                                </div>

                                <Container id='containerRows'>

                                    {
                                        filteredRecords.map(
                                            e => <div key={e._id} id='rowsDiv'>

                                                <div id='nameItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.name.split(" ")[0]} {e.name.split(" ")[1]}
                                                    </Typography>
                                                </div>

                                                <div id='priceItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.action}
                                                    </Typography>
                                                </div>

                                                <div id='quantityItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.type}
                                                    </Typography>
                                                </div>

                                                <div id='categoryItemContainer'>
                                                    <Typography variant='body2'>
                                                        {e.user.name}
                                                    </Typography>
                                                </div>

                                                <div id='userItemContainer'>
                                                    <Typography variant='body2'>
                                                        {moment(e.date).tz("America/Argentina/Buenos_Aires").format('LLL')}
                                                    </Typography>
                                                </div>

                                                <div id='menuIconDiv'>
                                                    <IconButton
                                                        size="large"
                                                        aria-label="account of current user"
                                                        aria-controls="menu-appbar"
                                                        aria-haspopup="true"
                                                        onClick={(event) => handleMenu(event, e)}
                                                        color="inherit"
                                                        id='productMenuIcon'
                                                    >
                                                        <VisibilityIcon />
                                                    </IconButton>
                                                </div>

                                            </div>
                                        )
                                    }

                                </Container>

                                <Container id='paginationDiv'>
                                    {
                                        (filteredRecords && filteredRecords?.length !== 0)
                                        &&
                                        <Stack
                                            spacing={2}>
                                            <Pagination
                                                size='large'
                                                aria-current='page'
                                                defaultPage={1}
                                                page={Number(pagePath)}
                                                count={Math.ceil(parseInt(records?.length) / 8)}
                                                onChange={handlePaginationChange}

                                            />
                                        </Stack>
                                    }
                                </Container>

                            </Container>

                        </Box>
                        :
                        <>
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation="wave" />
                            <Skeleton height={80} animation={false} />
                        </>
                }
            </div>
        </div>
    );
}
