import React, { forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { uiCloseSuccessAlert } from '../../../actions/ui';

const Alert = forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});


export const SuccessAlert = () => {

    const dispatch = useDispatch();

    const { successAlert } = useSelector(state => state.ui);

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));



    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        dispatch(uiCloseSuccessAlert())
    };


    return (
        <Snackbar open={successAlert.status} autoHideDuration={3000} onClose={handleClose} >
            <Alert onClose={handleClose} severity="success" sx={{
                width: '100%',
                marginBottom: (sm) && '85vh'

            }}>
                {successAlert.title}
            </Alert>
        </Snackbar>
    );
};