import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router';

import { chain } from 'mathjs'

import { Image } from 'cloudinary-react';

import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Container from '@mui/material/Container';
import CloseIcon from '@mui/icons-material/Close';
import { Button, ListItem, Typography } from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { Footer } from '../ui/Footer';
import { DialogBuy } from '../dashboard/alerts/DialogBuy';

import { salesStartAddNew } from '../../actions/sales';
import { userStartDeletedCart } from '../../actions/users';


export const CartScreen = () => {

    const navigate = useNavigate();
    const dispatch = useDispatch();

    const { cart, uid } = useSelector(state => state.auth);

    const [totalPrice, setTotalPrice] = useState(0);
    const [dialogBuyOpen, setDialogBuyOpen] = useState(false);

    var widthContainer = 0;


    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));
    const xl = useMediaQuery(theme.breakpoints.down('xl'));
    const lg = useMediaQuery(theme.breakpoints.down('lg'));

    switch (true) {
        case sm:
            widthContainer = 90;
            break;

        case md:
            widthContainer = 80;
            break;

        case lg:
            widthContainer = 70;
            break;

        case xl:
            widthContainer = 60;
            break;

        default:
            break;
    }

    useEffect(() => {

        window.scroll(0, 0)

        let counter = 0;

        cart.forEach(e => {
            counter = counter + parseFloat(e.price)
        });

        setTotalPrice(counter)

    }, [cart]);


    const handleDeleteCart = (e, cartProduct) => {
        e.preventDefault();

        dispatch(userStartDeletedCart({ id: cartProduct._id, name: cartProduct.name, img: cartProduct.img }, uid));

    }

    const handleBuy = () => {

        setDialogBuyOpen(true);

    }


    return (
        <Box sx={{
            width: '100%',
            '& #closeIcon': {
                display: 'flex',
                justifySelf: 'flex-end'
            },
            marginTop: (sm) ? '20%' : '20vh',
        }}>
            <DialogBuy
                dialogBuyOpen={dialogBuyOpen}
                setDialogBuyOpen={setDialogBuyOpen}
                cart={cart}
            />
            <Container
                sx={{
                    backgroundColor: '#fff',
                    width: `${widthContainer}%`,
                    height: '50vh',
                    overflowY: 'scroll',
                    marginTop: (sm) ? '5%' : '1.5%',
                    padding: '2.5% 2.5% 2.5% 0',
                    borderRadius: '5px',
                }}
            >

                <>
                    {
                        (cart.length !== 0)
                            ?
                            <>
                                {
                                    cart.map(
                                        e =>
                                            <Box key={e._id}
                                                sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                }}
                                            >


                                                <IconButton sx={{
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    width: (sm) ? '20%' : '5%',
                                                    height: 'auto',
                                                    ':hover': {
                                                        background: 'none'
                                                    }
                                                }}
                                                    onClick={(event) => handleDeleteCart(event, e)}
                                                >
                                                    <CloseIcon sx={{
                                                        ':hover': {
                                                            backgroundColor: 'rgba(0,0,0,0.03)',
                                                            borderRadius: '30px',
                                                            padding: '10px'
                                                        }
                                                    }} />
                                                </IconButton>

                                                <Container

                                                    sx={{
                                                        display: 'flex',
                                                        justifyContent: 'space-between',
                                                        alignItems: 'center',
                                                        marginTop: '2.5%',
                                                        marginBottom: '5%',
                                                        '& #imageContainer': {
                                                            display: 'flex',
                                                            justifyContent: 'center',
                                                            minWidth: '10ch',
                                                            maxHeight: '10ch',
                                                        },
                                                        '& #productCartImage': {
                                                            maxWidth: '10ch',
                                                            maxHeight: '10ch',
                                                        },
                                                        '& #name': {
                                                            display: 'flex',
                                                            justifyContent: 'flex-start',
                                                            marginLeft: '2.5%',
                                                        },
                                                        '& #price': {
                                                            display: 'flex',
                                                        },
                                                        ':hover': {
                                                            cursor: 'pointer',
                                                            backgroundColor: 'rgba(0,0,0,0.025)',
                                                            borderRadius: '5px'
                                                        }
                                                    }}
                                                    onClick={() => navigate(`/product/${e._id}`, { replace: true })}
                                                >

                                                    <div id='imageContainer'>
                                                        <Image
                                                            id='productCartImage'
                                                            cloudName="the-kings-company"
                                                            publicId={e.img[0].imageUrl}
                                                            alt='Product'
                                                        />
                                                    </div>

                                                    {
                                                        (!sm)
                                                        &&
                                                        <Typography id='name'>
                                                            {e.name}
                                                        </Typography>
                                                    }

                                                    <Typography id='price'>
                                                        {`$${new Intl.NumberFormat('es-IN').format(e.price)}`}
                                                    </Typography>

                                                </Container>
                                            </Box>

                                    )
                                }
                            </>
                            :
                            <Typography variant='body1'>
                                An no hay ningn producto en el carrito!
                            </Typography>
                    }
                </>
            </Container>

            <Container
                sx={{
                    marginTop: (sm) ? '5%' : '1.5%',

                    display: 'flex',
                    flexWrap: 'wrap',
                    alignItems: 'center',
                    width: `${widthContainer}%`,
                    backgroundColor: '#fff',
                    height: '10vh',
                    paddingRight: '2.5vw',
                    borderRadius: '5px',
                }}
            >


                <Container sx={{
                    width: '95%',
                    display: 'flex',
                    justifyContent: 'space-between',
                    borderBottom: '1px solid gray'
                }}>

                    <Typography id='monto'>
                        Monto total a pagar
                    </Typography>

                    <Typography id='price'>
                        {`$${new Intl.NumberFormat('es-IN').format(totalPrice)}`}
                    </Typography>
                </Container>


            </Container>

            <Container sx={{
                width: `${widthContainer}%`,
                display: 'flex',
                justifyContent: 'center',
                marginTop: (sm) ? '10%' : '2.5%',
                marginBottom: (sm) ? '20%' : '5%',
            }}>
                <Button sx={{
                    width: '10%',
                    minWidth: '200px',
                    display: 'flex',
                    justifySelf: 'flex-end',
                    backgroundColor: '#F7DC6F',
                    color: '#000',
                    ':hover': {
                        backgroundColor: '#F29102',
                        color: '#fff',
                    },
                }}
                    variant='contained'
                    onClick={handleBuy}
                >
                    Finalizar Compra
                </Button>
            </Container>
            <Footer />
        </Box>
    );
};